te;\r\n    top: 0;\r\n    left: 0;\r\n    background-color: rgba(52,73,94,0.75);\r\n}\r\n\r\n.more-templates {\r\n    display: block;\r\n    color: #17a2b8;\r\n    background-color: transparent;\r\n    background-image: none;\r\n    border-color: #17a2b8;\r\n    font-size: 16px;\r\n    margin-top: 10px;\r\n}\r\n\r\n.more-templates:not(:disabled):not(.disabled).active:focus {\r\n        box-shadow: 0 0 0 0.2rem rgba(23, 162, 184, 0.5);\r\n    }\r\n\r\n.more-templates:focus, .more-templates.focus {\r\n        box-shadow: 0 0 0 0.2rem rgba(23, 162, 184, 0.5);\r\n    }\r\n\r\n.more-templates:not(:disabled):not(.disabled):active, .more-templates:not(:disabled):not(.disabled).active {\r\n        color: #fff;\r\n        background-color: #17a2b8;\r\n        border-color: #17a2b8;\r\n    }\r\n\r\n.more-templates:hover {\r\n        color: #fff;\r\n        background-color: #17a2b8;\r\n        border-color: #17a2b8;\r\n    }\r\n\r\n@media screen and (max-width: 768px) {\r\n    .col-sm-4 {\r\n        text-align: center;\r\n        margin: 25px 0;\r\n    }\r\n}\r\n\r\n@media screen and (max-width: 992px) {\r\n    .parent-container {\r\n        width: 100%;\r\n        margin: 20px 0px\r\n    }\r\n\r\n    .wrapper {\r\n        padding: 0 20px;\r\n    }\r\n\r\n    .showcase {\r\n        margin-bottom: 0;\r\n    }\r\n\r\n    .our-values {\r\n        padding-top: 0;\r\n    }\r\n}\r\n"